.checkout-page {
  padding: 10em 0 0 0;
  background-image: url("../Assets/inner-hero-shape-2.png");
  background-repeat: no-repeat;
  background-size: 100%;
}

.error-message {
  text-align: center;
  color: red;
  font-size: 1.2em;
}

.already-registered-prompt {
  display: flex;
  background-color: #0c002c;
  padding: 1em 3em;
  margin: 0 0 2em 0;
  border-radius: 25px;
  justify-content: space-between;
  box-shadow: 0.3px 1.2px 1.6px rgba(0, 0, 0, 0.017),
    0.6px 2.7px 3.6px rgba(0, 0, 0, 0.024),
    1.1px 4.7px 6.3px rgba(0, 0, 0, 0.03), 1.8px 7.5px 10px rgba(0, 0, 0, 0.035),
    2.7px 11.6px 15.5px rgba(0, 0, 0, 0.04),
    4.2px 18.1px 24.1px rgba(0, 0, 0, 0.046), 7px 30px 40px rgba(0, 0, 0, 0.053),
    14px 60px 80px rgba(0, 0, 0, 0.07);
  p {
    color: white;
    font-size: 1.6em;
    span {
      color: #c4cbf9;
      font-size: 0.7em;
      display: inline-block;
      margin: 20px 0 0 0;
    }
  }
  .fi {
    color: white;
    border: none;
    font-size: 1.3em;
    padding: 20px 20px 12px 20px;
    clip-path: circle(40%);
    cursor: pointer;
    background-image: -webkit-linear-gradient(
      7deg,
      rgb(236, 19, 121) 0%,
      rgb(108, 0, 146) 100%
    );
    box-shadow: 0px 7px 7px 0px rgba(163, 15, 110, 0.35);
  }
  a {
    background: none;
    border: none;
    display: flex;
    align-items: center;
    cursor: pointer;
    p {
      font-size: 1.4em;
    }
  }
}

.checkout-container {
  background-color: #12023e;
  padding: 2em;
  border-radius: 25px;
  margin: 0 auto;
  display: flex;
  width: fit-content;
  .contact-details-wrapper {
    box-shadow: 0.3px 1.2px 1.6px rgba(0, 0, 0, 0.017),
      0.6px 2.7px 3.6px rgba(0, 0, 0, 0.024),
      1.1px 4.7px 6.3px rgba(0, 0, 0, 0.03),
      1.8px 7.5px 10px rgba(0, 0, 0, 0.035),
      2.7px 11.6px 15.5px rgba(0, 0, 0, 0.04),
      4.2px 18.1px 24.1px rgba(0, 0, 0, 0.046),
      7px 30px 40px rgba(0, 0, 0, 0.053), 14px 60px 80px rgba(0, 0, 0, 0.07);
    padding: 1em 3em;
    border-radius: 25px;
    background-color: #0c002c;
    p {
      color: white;
      font-size: 2em;
    }
    form {
      .form-group {
        margin: 0 0 30px 0;
      }
      input {
        padding: 20px;
        border: 1px solid #6472d99a;
        background: none;
        color: white;
        font-family: "Josefin Sans", sans-serif;
        font-size: 1.1em;
        display: block;
        outline: none;
        border-radius: 8px;
        -webkit-border-radius: 8px;
        width: 100%;
        box-sizing: border-box;
      }
      input::placeholder {
        color: #c8d0fcc9;
      }
      input:focus {
        border: 1px solid #fd7e14;
      }
      label {
        display: block;
        color: white;
        font-size: 1.2em;
        margin: 0 0 10px 0;
      }
    }
  }
  .total-wrapper {
    box-shadow: 0.3px 1.2px 1.6px rgba(0, 0, 0, 0.017),
      0.6px 2.7px 3.6px rgba(0, 0, 0, 0.024),
      1.1px 4.7px 6.3px rgba(0, 0, 0, 0.03),
      1.8px 7.5px 10px rgba(0, 0, 0, 0.035),
      2.7px 11.6px 15.5px rgba(0, 0, 0, 0.04),
      4.2px 18.1px 24.1px rgba(0, 0, 0, 0.046),
      7px 30px 40px rgba(0, 0, 0, 0.053), 14px 60px 80px rgba(0, 0, 0, 0.07);
    padding: 1em 3em;
    border-radius: 25px;
    background-color: #0c002c;
    height: fit-content;
    margin: 0 0 0 2em;
    width: 400px;
    p {
      color: white;
    }
    .card-name {
      font-size: 2em;
    }
    .ticket-wrapper {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin: 0 0 1em 0;
      .ticket {
        font-size: 1.2em;
        margin: 0;
        span {
          color: #24e980;
          font-size: 0.8em;
        }
      }
      .ticket-price {
        font-size: 1.5em;
      }
    }
    .total-price {
      font-size: 25px;
      border-top: 1px solid #6472d99a;
      padding: 20px 0;
      span {
        display: block;
        margin: 10px 0 0 0;
      }
    }
  }
  .payment-method {
    div {
      display: flex;
      button {
        background: none;
        padding: 10px 25px;
        border-radius: 10px;
        border: 1px solid;
        cursor: pointer;
      }
      .credit-card {
        color: white;
        .fi {
          margin: 0;
          display: block;
          font-size: 35px;
        }
        margin: 0 15px 0 0;
      }
      .paysafe {
        img {
          height: 30px;
        }
      }
    }
  }
  .finish-order {
    background-image: -webkit-linear-gradient(7deg, #ec1379 0%, #6c0092 100%);
    border: none;
    border-radius: 50px;
    font-size: 1.2em;
    font-weight: 500;
    color: white;
    font-family: "Josefin Sans", sans-serif;
    padding: 1em 2em;
    display: block;
    width: fit-content;
    margin: 30px auto -55px;
    cursor: pointer;
    box-shadow: 0px 17px 40px 0px rgba(201, 3, 112, 0.35);
  }
  .confirmations-container {
    margin: 2em 0 0 0;
    .input-group {
      display: flex;
      margin: 0;
      align-items: center;
      cursor: pointer;
      user-select: none;
      p {
        color: white;
        font-size: 1.2em;
        font-weight: 400;
        margin: 0;
      }
      .checkbox {
        margin: 0 15px 0 0;
        border: 1px solid white;
        height: fit-content;
        padding: 7px;
        border-radius: 5px;
        height: 10px;
        width: 10px;
        .fi {
          color: greenyellow;
          display: block;
          transform: translate(-3px, -2px);
        }
      }
    }
    .input-group:first-child {
      margin: 0 0 25px 0;
    }
  }
}

@media only screen and (max-width: 1350px) {
  .checkout-container {
    flex-direction: column;
    .total-wrapper {
      padding: 1em 3em;
      margin: 2em 0 00;
      width: 100%;
      box-sizing: border-box;
    }
  }
}

@media only screen and (max-width: 850px) {
  .already-registered-prompt {
    p {
      font-size: 1.2em;
      span {
        margin: 10px 0 0 0;
      }
    }
    .fi {
      font-size: 1em;
    }
    a {
      p {
        font-size: 1.2em;
      }
    }
  }
  .checkout-container {
    margin: 0 2em;
    padding: 2em 2em 4em;
    .contact-details-wrapper {
      p {
        color: white;
        font-size: 1.5em;
      }
      form {
        input {
          font-size: 1em;
        }
        label {
          font-size: 1em;
        }
      }
    }
  }
}

