    }
    .finish-order {
      background-image: -webkit-linear-gradient(7deg, #ec1379 0%, #6c0092 100%);
      border: none;
      border-radius: 50px;
      font-size: 1em;
      font-weight: 500;
      color: white;
      font-family: "Josefin Sans", sans-serif;
      padding: 1em 2em;
